import { prisma } from "@/lib/prisma"
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card"
import { FileText, ExternalLink } from "lucide-react"
import Link from "next/link"
import { Badge } from "@/components/ui/badge"
import { FileSearch } from "@/components/files/file-search"
import { AddFileDialog } from "@/components/files/add-file-dialog"

import { getUsers } from "@/actions/user-actions"
import { FileFilters } from "@/components/files/file-filters"

export default async function FilesPage({ searchParams }: { searchParams: Promise<{ query?: string, projectId?: string, userId?: string, taskType?: string }> }) {
    const { query, projectId, userId, taskType } = await searchParams

    const whereClause: any = {
        driveLink: { not: null },
    }

    if (query) {
        whereClause.title = { contains: query }
    }
    if (projectId && projectId !== "all") {
        whereClause.projectId = projectId
    }
    if (userId && userId !== "all") {
        whereClause.assigneeId = userId
    }
    if (taskType && taskType !== "all") {
        whereClause.taskType = taskType
    }

    const files = await prisma.workItem.findMany({
        where: whereClause,
        orderBy: { updatedAt: 'desc' },
        include: {
            project: true,
            assignee: true
        }
    })

    const projects = await prisma.project.findMany({
        orderBy: { name: 'asc' }
    })

    // Aggregate unique task types across all projects
    const allTaskTypes = Array.from(new Set(
        projects.flatMap(p => p.allowedTaskTypes ? p.allowedTaskTypes.split(",").map(t => t.trim()) : [])
    )).filter(Boolean).sort()

    // @ts-ignore
    const users = await getUsers()

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between gap-4">
                <div>
                    <h1 className="text-3xl font-bold tracking-tight">Files</h1>
                    <p className="text-muted-foreground">
                        Central repository of all deliverables.
                    </p>
                </div>
                <div className="flex items-center gap-2">
                    {/* @ts-ignore */}
                    <FileFilters projects={projects} users={users} taskTypes={allTaskTypes} />
                    <FileSearch />
                    <AddFileDialog projects={projects} />
                </div>
            </div>

            <div className="grid gap-6">
                <Card>
                    <CardHeader>
                        <CardTitle>Documents</CardTitle>
                        <CardDescription>
                            Access documents linked to tasks or uploaded directly.
                        </CardDescription>
                    </CardHeader>
                    <CardContent>
                        {files.length === 0 ? (
                            <div className="text-center py-12 text-muted-foreground bg-muted/20 rounded-lg border-dashed border-2">
                                <FileText className="h-10 w-10 mx-auto mb-3 opacity-20" />
                                <p>No files found.</p>
                            </div>
                        ) : (
                            <div className="border rounded-lg divide-y">
                                {files.map((file) => (
                                    <div key={file.id} className="p-4 flex items-center justify-between hover:bg-muted/50 transition-colors">
                                        <div className="space-y-1">
                                            <div className="font-medium flex items-center gap-2">
                                                <FileText className="h-4 w-4 text-primary" />
                                                {file.title}
                                            </div>
                                            <div className="text-sm text-muted-foreground flex items-center gap-2">
                                                {file.project && <Badge variant="outline">{file.project.name}</Badge>}

                                                <span className="text-xs text-muted-foreground">• {file.updatedAt.toLocaleDateString()}</span>
                                                {file.assignee && <span className="text-xs text-muted-foreground max-sm:hidden">• by {file.assignee.name}</span>}
                                            </div>
                                        </div>
                                        <div className="flex items-center gap-2">
                                            {file.driveLink && (
                                                <Link
                                                    href={file.driveLink}
                                                    target="_blank"
                                                    rel="noopener noreferrer"
                                                    className="inline-flex items-center gap-1 text-sm font-medium text-blue-600 hover:underline"
                                                >
                                                    Open <ExternalLink className="h-3 w-3" />
                                                </Link>
                                            )}
                                        </div>
                                    </div>
                                ))}
                            </div>
                        )}
                    </CardContent>
                </Card>
            </div>
        </div>
    )
}
